import {
  promised
} from "q";

export function getResults(query) {
  if (query) {
    let URL = `${location.origin}/search/?searchString=${query}`;
    return fetch(URL)
      .then(function (response) {
        return response.json()
      })
      .then(function (recipes) {
        return recipes;
      })
  } else {
    let p = new promised(() => "no query");
  }
}
